import 'zone.js'; // Required for Angular
import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppModule } from './app/app.module';
import { environment } from './environments/environment';


if (environment.production) {
  enableProdMode();
}
console.log('office pre init');
if(window.location.search.indexOf("displayDialog") === -1){
console.log('normal outlook side panel');
Office.initialize = reason => {
  init();
};
}else{
if(typeof Office === "undefined"){
  console.log(Office);
  console.log('dialog with office not already loaded');
  Office.initialize = reason => {
    init();
  };
}else{
  console.log('dialog with office already loaded');
  init();
}
}

function init (){
console.log('init');
const isMac = !!navigator.platform && 
navigator.platform.toUpperCase().indexOf('MAC') >= 0;
let ext:any;
ext = window.external;
if (isMac && window.external && !ext.RegisterEvent) {
  ext.RegisterEvent = () => { };
}

document.getElementById('sideload-msg').style.display = 'none';
// Bootstrap the app
platformBrowserDynamic().bootstrapModule(AppModule).catch(error => console.error(error));
}