
import { Component, OnInit, NgZone } from '@angular/core';
import { AppService } from '../../services/app.service';
import { BaseComponent } from '../../shared/components/base.component';
import {
    trigger,
    state,
    style,
    animate,
    transition,
} from '@angular/animations';

import * as moment from 'moment';

@Component({
    selector: 'app-home',
    templateUrl: './home.template.html',
    styleUrls: ['./home.styles.scss'],
    animations: [
        trigger('EnterLeave', [
            state('flyIn', style({ transform: 'translateX(0)', boxShadow: '100px 0px 0px 1000px rgba(0,0,0,0.0)' })),
            transition(':enter', [
              style({ transform: 'translateX(-100%)', boxShadow: '100px 0px 0px 1000px rgba(0,0,0,0.3)' }),
              animate('0.2s 0.2s ease-out')
            ]),
            transition(':leave', [
              animate('0.2s ease-out', style({ transform: 'translateX(-100%)', boxShadow: '100px 0px 0px 1000px rgba(0,0,0,0.0)' }))
            ])
          ])
    ],
})
export class HomeComponent extends BaseComponent implements OnInit {

    public model: any = {};
    public display: any = {};
    public timers: any = {};

    public Office:any;

    public mailbox;
    public item;
    public regions;
    public version;

    public meetingList = [
        "One-Time Meeting ID",
        "Personal Meeting ID (PMI) "
    ]

    constructor (
        private zone: NgZone,
        private service: AppService
    ) 
    {
        super();
        this.model.routing = {};
        this.model.meetingId = 1;
        this.display.show_advanced = false;
        
    }


    async run() {
       if(this.model.processing){
           return;
       }
        let isInstant = (this.model.meetingId === 0);
        this.model.processing = true;
        if(!this.service.Settings.get('mock')){
            this.service.Zoom.register({
                email: this.model.user,
                start: this.model.start,
                end: this.model.end,
                alternative_host: this.model.alternativeHost,
                password: this.model.password,
                instant: isInstant
            }).then((resp: any)=>{
                console.log('this should return some kind of url maybe?', resp);
                this.model.join_url = resp.join_url;
                this.model.meeting_id = resp.id;
                this.setLocation();
            });
        }else{
            this.model.join_url = "https://join.zoom.com/f/12345678";
            this.model.meeting_id = 987654321;
            this.setLocation();
        }
    }

    public ngOnInit() {
        console.log('ON INIT');

        //break;
        return;

        this.item = Office.context.mailbox.item;
        this.Office = Office;
        this.model.filter = "";

        this.model.processing = false;
        
        this.getStartTime();
        this.getEndTime();

        this.getAttendees(); 
        this.getOptionalAttendees(); 

        this.getUserProfile();

        console.log('localstorage?', this.isLocalStorageNameSupported() );

        this.init();
    }

    public init() {
        console.log('INIT');
        if (!this.service.ready()) {
            return setTimeout(() => this.init(), 200);
        }
        this.regions = this.service.Settings.get('regions');
        this.model.locations = Object.keys(this.regions).map((key, index) => {
            return {
                name: this.regions[key].name,
                code: key,
                phone: this.regions[key].phone
            }
        });
        this.model.filteredLocations = this.model.locations;
        this.model.selectedLocations = [];

        if(!this.service.Settings.get('mock')){
            this.service.Zoom.getId(this.model.user).then((resp: any)=>{
                console.log('this should be pmi?', resp);
                let pmi = resp.pmi;
                this.model.pmi = pmi;
                this.model.user_name = resp.first_name + " " + resp.last_name;
                this.meetingList[1] += pmi.toString().substring(0, 3) + '-' + pmi.toString().substring(3, 6) + "-" + pmi.toString().substring(6);
            });
        }else{
                let pmi = 1234567890;
                this.model.pmi = pmi;
                this.model.user_name = "Ben Hoad";
                this.meetingList[1] += pmi.toString().substring(0, 3) + '-' + pmi.toString().substring(3, 6) + "-" + pmi.toString().substring(6);
            
        }
        console.log('old settings');
        this.version = this.service.Settings.get('version');
        this.loadOldSettings();
    }

    public filterChange(event){
        console.log(this.model.filter);

        if(this.model.filter.length == 0){
            this.model.filteredLocations = this.model.locations;
        }else{
            this.model.filteredLocations = this.model.locations.filter((location)=>{return (location.name.toLowerCase().indexOf(this.model.filter.toLowerCase()) > -1 || location.code.toLowerCase().indexOf(this.model.filter.toLowerCase()) > -1)});
        }
    }

    public checkboxChange(event){
        console.log('event: ', event);
        setTimeout(()=> {
            this.updateSelectionList();
        }, 50)
    }

    public updateSelectionList(){
        this.model.selectedLocations = [];
        //go through locations
        for(let location of this.model.locations){
            //find checked and add them to a list
            if(location.selected){
                if(this.model.primaryLocation && location.code === this.model.primaryLocation.code){
                    this.model.selectedLocations.unshift(location);
                }else{
                    this.model.selectedLocations.push(location);
                }
            }
        }
        //if list too long pop the last one
        
        if(this.model.selectedLocations.length > 5){
            this.model.selectedLocations[5].selected = false;
            this.model.selectedLocations.pop();
        }
    }

    public initBuilding() {
        console.log('getting buildings');
        const list = this.service.Buildings.list();
        if (!list || list.length <= 0) {
            return setTimeout(() => this.initBuilding(), 500);
        }
        this.model.bld_count = list.length;
        let bld = '';
        if (localStorage) {
            bld = localStorage.getItem('STAFF.building');
        }
        this.model.show_building = !bld && this.model.bld_count > 1;
        this.service.Buildings.listen((building) => this.model.bld = building);
    }

   
    public setPrimaryLocation(location, event){
        this.model.primaryLocation = location;
        console.log('updated primary:', location);
        this.updateSelectionList();
    }
    

    // Get the start time of the item that the user is composing.
    public getStartTime() {
        this.item.start.getAsync(
            (asyncResult:any) =>  {
                if (asyncResult.status == Office.AsyncResultStatus.Failed){
                    console.log (asyncResult.error.message);
                }
                else {
                    console.log('start time', asyncResult);
                    if(asyncResult && asyncResult.value){
                        this.model.start = asyncResult.value;
                        this.zone.run(() => { })
                    }
                }
            });
    }

    public getEndTime() {
        this.item.end.getAsync(
            (asyncResult:any) =>{
                if (asyncResult.status == Office.AsyncResultStatus.Failed){
                    console.log (asyncResult.error.message);
                }
                else {
                    console.log('end time', asyncResult);
                    if(asyncResult && asyncResult.value){
                        this.model.end = asyncResult.value;
                        this.zone.run(() => { })
                    }             
                }
            });
    }

    public getAttendees() {
        this.item.requiredAttendees.getAsync(
            (asyncResult:any) => {
                if (asyncResult.status == Office.AsyncResultStatus.Failed){
                    console.log (asyncResult.error.message);
                }
                else {
                    console.log('async result required',asyncResult);
                    if(asyncResult && asyncResult.value){
                        this.model.requiredAttendees = asyncResult.value;
                        this.zone.run(() => { })
                    }
                  
                }
            });

        
    }

    public getOptionalAttendees() {
        this.item.optionalAttendees.getAsync(
            (asyncResult:any) =>{
                if (asyncResult.status == Office.AsyncResultStatus.Failed){
                    console.log (asyncResult.error.message);
                }
                else {
                    console.log('async result optional',asyncResult);
                    this.model.optionalAttendees = asyncResult.value;
                    this.zone.run(() => { })
             
                }
            });
    }

    public getUserProfile(){
        this.model.user = Office.context.mailbox.userProfile.emailAddress;
        console.log('user 01',this.model.user);
    }

    public setLocation(){
        let details = " || ";
        let pin = this.model.pmi;

        if(this.model.meetingId == 0){
            pin = this.model.meeting_id;
        }


        //add Zoom Join URL
        details += this.model.join_url;

        //add list of countries
        console.log('selected locations: ', this.model.selectedLocations);
        if(this.model.selectedLocations){
            for(let location of this.model.selectedLocations){
                details += " || " + location.code.toUpperCase() + " "+ location.phone.replace(/\s/g , "-") +",,"+pin+"#";
            }
        }
        

        //add to location field
        this.item.location.getAsync((result)=>{
            result = result.value;
            let insertedDetails = "";
            console.log('result: ',result);

            
            if(result.indexOf(' || ') < 0){

                //doesn't exist
                insertedDetails = result + details;

            }else{

                //does exist
                let endIndex = result.indexOf(' || ');
                insertedDetails = result.substring(0, endIndex) + details;
                

            }

            this.item.location.setAsync(insertedDetails);
        });





        let splitChar = "_______________________________________________________________";
        
        //construct the body;

        let html = "\
            <div style='padding:20px;'>\
            <p>Hi there,</p>\
            <p>"+ this.model.user_name +" is inviting you to scheduled Zoom meeting.</p>\
            <a style='display:inline-block; margin-left:10px; color: #5491E9' href='"+ this.model.join_url +"'><h1 style='font-weight:300;'>Join Zoom Meeting</h1></a>\
            <table style='padding:10px'>\
                <tr style='vertical-align:top'>\
                    <td style=' vertical-align:top'>Phone One-Tap:</td>\
                    <td style='line-height:1.2em;'>";
                    //add dial-ins
                    if(this.model.selectedLocations){
                        for(let location of this.model.selectedLocations){
                            html += "<a style='color: #5491E9; margin-bottom:8px;' href=tel:"+ location.phone.replace(/\s/g , "-") +",,"+pin +">" + location.code.toUpperCase() + " "+ location.phone.replace(/\s/g , "-") +",,"+pin+"#</a> <br/>";
                        }
                    }
                    
                html +="</td>\
                </tr>\
                <tr  style=''>\
                    <td style=''>Meeting URL:</td>\
                    <td style=''><a style='color: #5491E9;' href="+ this.model.join_url  +">"+ this.model.join_url  +"</a></td>\
                </tr> \
            </table>\
            </div>\
        ";
        
                    console.log('getting body');
        this.item.body.getAsync("html",((result)=>{
            result = result.value;
            let insertedHtml = "";

            console.log('getting body: ', result);
            console.log(result);

            if(result.indexOf(splitChar) < 0){
                //doesn't exist
                console.log('does not exist');

                //lazily split at </body> tag
                let resultA = result.split('</body>');
                result = resultA[0];

                result += splitChar;
                result += html;
                result += splitChar;

                result += "</body>" + resultA[1];

                insertedHtml = result;
                console.log(insertedHtml);

            }else{
                //does exist
                console.log('does exist');
                let regexQuery = new RegExp( splitChar + "([\\s\\S]*?)" + splitChar, "g");
                html = splitChar + html + splitChar;

                result = result.replace(regexQuery, html);

                insertedHtml = result;
            }

            console.log(insertedHtml);
            console.log(insertedHtml);

            this.item.body.setAsync(insertedHtml, { coercionType: "html"}, ()=>{
                console.log('body set!!!!!!');
                this.model.processing = false;
                
                //close drawer
                this.Office.context.ui.closeContainer();
            });
            this.saveCurrentSettings();
            
            

        }));
        
        
    }    

    public isLocalStorageNameSupported() {
        var testKey = 'test', storage = window.localStorage;
        try {
          storage.setItem(testKey, '1');
          storage.removeItem(testKey);
          return true;
        } catch (error) {
          return false;
        }
      }

      public saveCurrentSettings(){
        let settings = {
            selectedLocations: this.model.selectedLocations,
            meetingId: this.model.meetingId,
            alternativeHost: this.model.alternativeHost,
            primaryLocation: this.model.primaryLocation
        }

        window.localStorage.setItem('settings', JSON.stringify(settings));
      }

      public loadOldSettings(){
        let settings = JSON.parse(window.localStorage.getItem('settings'));

        if(settings){
            this.model.meetingId = settings.meetingId;
            this.model.alternativeHost = settings.alternativeHost;
            this.model.primaryLocation = settings.primaryLocation;

            if(this.model.alternativeHost && this.model.alternativeHost !== ''){
                this.display.show_advanced = true;
            }

            let codeString = "";

            for(let location of settings.selectedLocations){
                codeString += location.code+"-";
            }

            for(let location of this.model.locations){
                if(codeString.indexOf(location.code) >= 0){
                    location.selected = true;
                }
            }

            this.updateSelectionList();
        }
      }


      public toggleAdvancedOptions(e){
        this.display.show_advanced = !this.display.show_advanced;
      }

      public goToConverge(e){
        window.open("https://roombooking.intranet.mckinsey.com/")
      }
}